import { ref, onMounted } from "vue";
import axios from "axios";
import png from "@/util/useAdd.gif";
import { useRouter } from "vue-router";
// jwtËß£ÊûêÊèí‰ª∂
import jwtDecode from "jwt-decode";
export default {
  setup() {
    const image = [png];
    const show_1 = ref(false);
    const show_2 = ref(false);
    const isLoading = ref(false);
    const router = useRouter();
    onMounted(() => {
      fetchLoginToken();
    });
    const token = localStorage.getItem("jwtToken"); // ‰ªélocalStorageËé∑ÂèñJWT‰ª§Áâå
    if (!token) {
      router.replace("/login");
    }
    const headers = {
      Authorization: `Bearer ${token}`
    };
    let userId = "";
    const fetchLoginToken = () => {
      axios.post("/api/loginToken?token=" + token).then(response => {
        if (response.data.code == 0) {
          console.error(response.data.data);
          router.replace("/login");
          return;
        }

        // Ëé∑Âèñid
        const decodedToken = jwtDecode(token);
        // ‰ªéËß£Á†ÅÂêéÁöÑ‰ª§Áâå‰∏≠Ëé∑ÂèñÁâπÂÆöÁöÑÊï∞ÊçÆ
        userId = decodedToken.id; // ‰ªé‰ª§Áâå‰∏≠Ëé∑ÂèñÁî®Êà∑ID
        // Âú®ËøôÈáåÂ§ÑÁêÜÁôªÂΩï‰ª§ÁâåÊé•Âè£ÁöÑÂìçÂ∫î
        // Â¶ÇÊûúÈúÄË¶ÅÊâßË°å‰∏Ä‰∫õÁâπÂÆöÁöÑÊìç‰ΩúÔºåÂèØ‰ª•Âú®ËøôÈáåÊ∑ªÂä†‰ª£Á†Å
      }).catch(error => {
        console.error("ËØ∑Ê±ÇloginTokenÊé•Âè£Â§±Ë¥•", error);
        router.replace("/login");
      });
    };
    const checkConfirm = () => {
      isLoading.value = true; // ÊòæÁ§∫Âä†ËΩΩ‰∏≠ÊïàÊûú
      onLogout();
    };
    const checkCancel = () => {
      return;
    };
    const logout = () => {
      show_2.value = true;
    };
    const onLogout = () => {
      axios.put(`/api/deleteUser?id=${userId}`, "", {
        headers
      }).then(response => {
        // Â∞ÜÂìçÂ∫îËΩ¨Êç¢‰∏∫JSON
        const responseData = response.data;

        // Ê£ÄÊü•codeÊòØÂê¶Á≠â‰∫é1
        if (responseData.code === 1) {
          // Â§ÑÁêÜÂÆåÊàêÊàêÂäüÁöÑÈÄªËæë
          // ÂÆåÊàêÊàêÂäüÂêéÈáçÊñ∞Âä†ËΩΩÈ°µÈù¢
          // Âà†Èô§Êú¨Âú∞Â≠òÂÇ®ÁöÑ token
          localStorage.removeItem("jwtToken");
          // ‰ΩøÁî® Vue Router ËøõË°åÈ°µÈù¢Ë∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µÈù¢
          router.replace("/login");
        } else {
          console.error("Âà†Èô§Áî®Êà∑Â§±Ë¥•", responseData);
          // Âú®ËøôÈáåÂèØ‰ª•Â§ÑÁêÜÂÖ∂‰ªñÈîôËØØÊÉÖÂÜµ
        }
      }).catch(error => {
        // Â§ÑÁêÜÂÆåÊàêÂ§±Ë¥•ÁöÑÈÄªËæë
        console.error("Âà†Èô§Áî®Êà∑Â§±Ë¥•", error);
      });
    };
    const onClickLeft = () => router.replace("/Setting");
    const id = router.currentRoute.value.params.id;
    let strippedId = id.replace(":", "");
    const username = ref("");
    const desc = ref("");
    const password = ref("");
    const fileList = ref([]);
    let imageUrl = ref("");
    const afterRead = file => {
      file.status = "uploading";
      file.message = "‰∏ä‰º†‰∏≠...";
      const formData = new FormData();
      formData.append("file", file.file);
      fetch("/api/upload", {
        method: "POST",
        headers: headers,
        // Ê∑ªÂä†ËØ∑Ê±ÇÂ§¥
        body: formData
      }).then(response => response.json()).then(data => {
        // ËøôÈáåÂÅáËÆæÊúçÂä°Âô®ËøîÂõû‰∏Ä‰∏™ÂåÖÂê´ÂõæÁâáURLÁöÑJSONÂØπË±°
        imageUrl.value = data.data;
        file.message = "‰∏ä‰º†ÊàêÂäü";
        file.status = "";
      }).catch(error => {
        console.error("‰∏ä‰º†Â§±Ë¥•Ôºö", error);
      });
    };
    const onSubmit = () => {
      // ÂàõÂª∫ÂåÖÂê´ÂèÇÊï∞ÁöÑËØ∑Ê±Ç‰Ωì
      const user = {
        // id‰∏çËÉΩÂä†value
        nameId: strippedId,
        name: username.value,
        userImage: imageUrl.value,
        userText: desc.value,
        password: password.value
      };
      isLoading.value = true;
      // ÂèëÈÄÅ POST ËØ∑Ê±ÇÂà∞ÊåáÂÆöÁöÑ URL
      fetch("/api/requireUser", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`
        },
        body: JSON.stringify(user)
      }).then(response => {
        console.log(response.data);
        try {
          show_1.value = true;
          // Á≠âÂæÖ3ÁßíÂêéÊâßË°åË∑ØÁî±Ë∑≥ËΩ¨
          setTimeout(() => {
            router.replace("/");
          }, 1000);
        } catch (error) {
          console.error("ËØ∑Ê±ÇrequireUserÊé•Âè£Â§±Ë¥•", error);
        }
      }).catch(error => {
        console.error("ËØ∑Ê±ÇrequireUserÊé•Âè£Â§±Ë¥•", error);
        // Â§ÑÁêÜËØ∑Ê±ÇÂ§±Ë¥•ÁöÑÊÉÖÂÜµ
      });

      isLoading.value = false;
    };
    return {
      show_1,
      show_2,
      image,
      onClickLeft,
      checkConfirm,
      checkCancel,
      logout,
      username,
      desc,
      password,
      onSubmit,
      fileList,
      afterRead
    };
  }
};